body {
  font-family: 'Poppins', sans-serif;
}
.App {
  text-align: center;
}
section {
  margin-bottom: 70px;
}

.transparent-header header {
  background: transparent;
}
.transparent-header .navbar-dark .navbar-nav .nav-link {
  color: #fff;
}

header {
  background: #0cad75;
  background: linear-gradient(90deg, rgb(10 179 119) 0%, rgb(10 175 118) 33%, rgb(14 171 117) 66%, rgb(16 167 115) 100%);
}
.navbar-dark .navbar-nav .nav-link {
  padding: 18px 16px;
}
.navbar-dark .navbar-brand {
  font-size: 22px;
}
.blog-card img {
  width: 100%;
  margin-bottom: 10px;
}
.blog-card {
  border: 1px solid #eee;
  padding: 0;
  box-shadow: 2px 3px 5px #eee;
}
.blog-card h2, .blog-card p {
  padding: 0 10px;
}
.intro img {
  width: 400px;
  margin-top: 10px;
  clip-path: polygon(10% 2%, 100% 0, 90% 98%, 0% 100%);
  clip-path: polygon(10% 0, 100% 4%, 90% 100%, 0 97%);

}
.intro {
  position: relative;
  min-height: 600px;
}
.intro:before {
  content: '';
  background-image: url('./assets/images/hero-bg.png');
  /* -webkit-clip-path: polygon(0 0, 75% 0, 55% 100%, 0% 100%);
  clip-path: polygon(0 0, 75% 0, 55% 100%, 0% 100%); */
  /* background-color: #009688; */
  width: 100%;
  height: 115%;
  position: absolute;
  z-index: -1;
  /* background-size: 150%;
  background-repeat: no-repeat; */
  left: 0;
  top: -80px;
  background-repeat: no-repeat;
  background-size: 118%;
}
/* .intro .container {
  background-image: url('./assets/images/header_behind_shape_1.png');
  background-size: cover;
  background-repeat: no-repeat;  
} */
.intro .btn-services-invert {
  display: inline-block;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  border-radius: 30px;
  padding: 15px 30px;
  border: 1.5px solid #fff;
  letter-spacing: 1px;
  text-transform: uppercase;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.intro .btn-services-invert:hover {
  color: #fff;
  background-color: #ffb600;
  border-color: #ffb600;
}
.intro .btn-services {
  display: inline-block;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  border-radius: 30px;
  padding: 15px 30px;
  border: 1.5px solid #ffb600;
  background: #ffb600;
  letter-spacing: 1px;
  text-transform: uppercase;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.intro .btn-services:hover {
  color: #fff;
  border-color: #fff;
  background-color: transparent;
}
.btn-services {
  display: inline-block;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  border-radius: 30px;
  padding: 15px 30px;
  border: 1.5px solid #ffb600;
  background: #ffb600;
  letter-spacing: 1px;
  text-transform: uppercase;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.btn-services:hover, .btn-services:focus {
  color: #ffb600;
  border-color: #ffb600;
  background-color: transparent;
  box-shadow: none;
}
.btn-group-sm>.btn, .btn-sm {
  padding: 8px 15px;
  font-size: .875rem;
  line-height: 1.5;
  /* border-radius: .2rem; */
}
.homepage .about-content {
  padding: 100px 80px;
}
.about-content {
  padding: 160px 80px;
}
.about-section {
  background: url(./assets/images/bg_elements.png) no-repeat;
}
.bg-sec-color {
  background: #0cad75!important;
}
footer p {
  color: #fff;
  margin: 8px 0;
  font-size: 15px;
}
footer {
  margin-top: 40px;
}
.contact-page footer {
  margin-top: 0;
}
.contact-page .map {
  height: 450px;
}

footer .fa{
  animation: heartbeat 1s infinite;
}

@keyframes heartbeat
{
  0%
  {
    transform: scale( .75 );
  }
  20%
  {
    transform: scale( 1 );
  }
  40%
  {
    transform: scale( .75 );
  }
  60%
  {
    transform: scale( 1 );
  }
  80%
  {
    transform: scale( .75 );
  }
  100%
  {
    transform: scale( .75 );
  }
}

.react-rotating-text-cursor {
  animation: blinking-cursor 0.8s cubic-bezier(0.68, 0.01, 0.01, 0.99) 0s infinite;
}

@keyframes blinking-cursor {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.rotating-text-heading > span {
  margin-left: 10px;
  /* color: #FFEB3B; */
  color: #FFB600;
  font-weight: 700;
}
.intro-content {
  color: #fff;
}
.main-text h3 {
  font-weight: 400;
  font-size: 19px;
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
  margin-top: 30px;
}
.main-text h1 {
  font-size: 42px;
  line-height: 42px;
  font-weight: 500;
  margin: 19px 0 10px;
  color: #fff;
  white-space: nowrap;
  position: relative;
  text-transform: uppercase;
}
.main-text p {
  margin: 11px 0 38px;
  font-size: 16px;
  line-height: 32px;
  font-weight: 400;
  font-family: 'Open Sans', sans-serif;
}

/* === Counter === */
.counter-section {
  background-color: #0cad75;
  padding-top: 80px;
  padding-bottom: 80px;
}
.counter-icon {
  font-size: 42px;
  margin-bottom: 20px;
}
.counter-name {
  font-size: 16px;
}
.blog-section .btn-sm {
  font-size: 12px;
  padding: 10px 20px;
}

.box-contact-form {
  /* padding: 42px 52px; */
  max-width: 460px;
  margin-left: auto;
  margin-right: auto;
  padding: 30px 45px;
  background: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 44px rgba(17, 29, 48, 0.06);
}
.box-contact-form input::placeholder, .box-contact-form textarea::placeholder {
  font-size: 14px;
}
.box-contact-form .btn {
  background: #0cad75;
  border-color: #0cad75;
  padding: 8px 45px;
  transition: all .3s ease-in-out;
  color: #fff;
}
.box-contact-form .btn:hover, .box-contact-form .btn:focus, .box-contact-form .btn:active {
  background-color: #079160 !important;
  border-color: #079160 !important;
  outline: none !important;
  box-shadow: none !important;
  color: #fff;
}
.contact-section .contact-content p {
  padding: 0 200px;
  margin-bottom: 60px;
  margin-top: 30px;
  color: #444;
}
.box-contact-form .form-control:focus {
  border: 1px solid #0cad75 !important;
  box-shadow: 0 0 0 1px #0cad75 !important;
}